@import url(./__button/popup__button.css);
@import url(./__text/popup__text.css);
@import url(./__container/popup__container.css);
@import url(./__title/popup__title.css);
@import url(./__close/popup__close.css);
@import url(./__container/_img/popup__container_img.css);
@import url(./__photo/popup__photo.css);
@import url(./__caption/popup__caption.css);


.popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0, .5);
    justify-content: center;
    align-items: center;
    display: flex;
    padding: 0;
    visibility: hidden;
    opacity: 0;
    transition: visibility 0.4s, opacity 0.4s linear;
    -moz-transition: all 0.4s;
  }
  
